    /*overflow: hidden;*/
    /*cursor: pointer;*/
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-items: center;
    /*height: auto;*/
    padding-top: 1px;

}

.path-display-title {
    margin-top: 5px;
    position: relative;
    font-size: 14px;
    /*margin: 10px;*/
    width: 210px;
    bottom: calc(100% + 50px); /* Position it just above the path-container */
    bottom: 5px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #598acd;
    padding: 5px; /* Optional: to add some spacing around the text */
    /*border-radius: 5px;*/
    color: white;
}

.path-display-path {
    position: relative;
    bottom: 5px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    /*justify-content: center;*/
    /*align-items: center;*/
    width: 220px;
    background-color: #f8f8f8;
    border: 1px solid #ccc;
    /*border-radius: 5px;*/
    padding: 5px;
    box-sizing: border-box;
    overflow-wrap: break-word; /* Ensures long words wrap to the next line */
    overflow: hidden;
    margin: 0;
}

.ndp-button-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: auto;
    padding-top: 20px;
}

.ndp-button {
    align-self: center;
    background-color: #598acd;
    border: 0;
    border-radius: 3px;
    box-sizing: border-box;
    color: white;
    font-size: 13px;
    height: 28px;
    margin-top: 5px;
    width: 200px;
    cursor: pointer;
}

.headers {
