    /*height: 28px;*/
    margin-top: 5px;
    width: 200px;
    cursor: pointer;
    padding-top: 20px;
    text-align: center;
}

.ndp-button:hover {
    background-color: #1573d7;
}

.ndp-logo, .ndp-logo-map {
    display: block;
    margin: 0 auto;
    width: 170px; /* Adjust the size as needed */
    height: 50px; /* Adjust the size as needed */
    padding-top: 10px;
}

.ndp-logo-map {
    height: 100px; /* Adjust the size as needed */
}

.spinner-container {
    display: flex;
    justify-content: center; /* Center horizontally */
}

